<template>
    <div>
        <div class="map" id="map"></div>
    </div>
</template>

<script>
import store from "../store/index.js";
var map; //    
var mapTypeControl;
// var markerPosition; // 
export default {
    computed: {
        messageComputed() {
            // console.log(store.state.change);
            return store.state.dongChange;
        },
        aptMessageComputed() {
            // console.log(store.state.change);
            return store.state.aptChange;
        },
    },
    mounted() {
        if (!store.state.dongChange && window.kakao && window.kakao.maps) {
            this.initMap();
        } else {
            this.addKakaoMapScript();
        }
    },
    methods: {
        addKakaoMapScript() {
            const script = document.createElement("script");
            /* global kakao */
            script.onload = () => kakao.maps.load(this.initMap);
            script.src =
                "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=278146fb736e61be5c8d95d8b24bc1c0";
            document.head.appendChild(script);
        },
        initMap() {
            var container = document.getElementById("map"); //   DOM 
            var options = {
                //     
                center: new kakao.maps.LatLng(37.5743822, 126.9688505), // .
                level: 5, // (,  )
            };

            map = new kakao.maps.Map(container, options); //    
            mapTypeControl = new kakao.maps.MapTypeControl();

            //     
            // kakao.maps.ControlPosition     TOPRIGHT   
            map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);
            map.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC);
        },
        setCenter(lat, lng) {
            //     
            var moveLatLon = new kakao.maps.LatLng(lat, lng);

            //    
            map.setCenter(moveLatLon);
        },

        panTo(lat, lng, positions) {
            //     
            var moveLatLon = new kakao.maps.LatLng(lat, lng);

            //     
            function makeOverListener(map, marker, infowindow) {
                return function () {
                    infowindow.open(map, marker);
                };
            }

            //     
            function makeOutListener(infowindow) {
                return function () {
                    infowindow.close();
                };
            }

            //  
            for (let i = 0; i < positions.length; i++) {
                var marker = new kakao.maps.Marker({
                    map: map,
                    position: new kakao.maps.LatLng(
                        positions[i].lat,
                        positions[i].lng
                    ),
                    clickable: true,
                });
                // console.log(positions);

                //        

                // ------------------------- //
                //        //
                // ------------------------- //
                // positions[i].aptName    .
                // console.log(positions[i].aptName);
                var iwContent =
                        '<div style="padding:5px;">' +
                        positions[i].aptName +
                        "</div>", //    HTML  document element 
                    iwRemoveable = true; // removeable  ture       x 

                //  
                var infowindow = new kakao.maps.InfoWindow({
                    content: iwContent,
                    removable: iwRemoveable,
                });
                //   

                kakao.maps.event.addListener(
                    marker,
                    // "click",
                    "mouseover",
                    makeOverListener(map, marker, infowindow)
                );
                kakao.maps.event.addListener(
                    marker,
                    "mouseout",
                    makeOutListener(infowindow)
                );
                marker.setMap(map);
            }
            //    
            //          

            map.panTo(moveLatLon);
        },
        //     
    },

    watch: {
        messageComputed: function () {
            if (store.state.dongChange && window.kakao && window.kakao.maps) {
                if (store.state.lat) {
                    this.initMap(); //    
                    console.log(store.state.positions);
                    this.panTo(
                        store.state.lat,
                        store.state.lng,
                        store.state.positions
                    );
                } else {
                    this.initMap();
                }
            } else {
                this.addKakaoMapScript();
            }
        },
        aptMessageComputed: function () {
            if (store.state.aptChange && window.kakao && window.kakao.maps) {
                if (store.state.lat) {
                    console.log(store.state.positions);
                    this.panTo(
                        store.state.lat,
                        store.state.lng,
                        store.state.positions
                    );
                } else {
                    this.initMap();
                }
            } else {
                this.addKakaoMapScript();
            }
        },
    },
};
</script>